#intro {
  background-color: #0e141e;
  padding: 30px;
  position: relative; }
  #intro .background {
    position: fixed;
    right: 0;
    bottom: 0;
    width: 30%;
    max-width: 350px;
    opacity: 0.1; }
  #intro .navbar {
    margin-bottom: 15vh; }
    #intro .navbar .buttons {
      margin-left: 30px; }
    #intro .navbar .nav-link {
      color: var(--gray-200);
      -webkit-transition: color .5s;
      -o-transition: color .5s;
      transition: color .5s; }
      #intro .navbar .nav-link:hover {
        color: #2EC866; }
  #intro .notice {
    border: 1px solid #e74c3c;
    padding: 10px;
    border-radius: 3px;
    font-family: var(--font-family-monospace);
    margin-top: 40px; }
  #intro p {
    line-height: 25px;
    color: #ced4da; }



